export type DisplayItemEvent = (item: IDisplayItem) => void;

export interface IDisplayItem {
    priority: number;
    onDisplay: DisplayItemEvent[];
    onClosed: DisplayItemEvent[];
    show(): void;
    hide(): void;
}

export class DisplayItem implements IDisplayItem {
    priority: number;
    onDisplay: DisplayItemEvent[] = [];
    onClosed: DisplayItemEvent[] = [];
    
    constructor(priority: number) {
        this.priority = priority;
    }

    show(): void {
        this.onDisplay.forEach(event => event(this));
    }

    hide(): void {
        this.onClosed.forEach(event => event(this));
    }
}
